<template>
  <div class="container">
    <div class="col-sm-10">
      <h1>Задачи</h1>
      <p>Всего задач: {{ numTasks }}</p>
      <p>Невыполненных: {{ numOngoing }}</p>
      <confirmation
                    :message="confirmationMessage"
                    :showConfirmation="showConfirmation">
      </confirmation>
      <button type="button"
              id="task-add"
              class="btn btn-success btn-sm align-left d-block"
              v-b-modal.todo-modal
              @click="createForm">
        Добавить задачу
      </button>

      <table class="table table-dark table-stripped table-hover">
        <thead class="thead-light">
          <tr>
            <th>Uid</th>
            <th>Описание</th>
            <th>Выполнена?</th>
            <th></th>
          </tr>
        </thead>

        <tbody>
          <tr v-for="(todo, index) in todos" :key="index">
            <td class="todo-uid">{{ todo.uid }}</td>
            <td>{{ todo.description }}</td>
            <td>
              <span v-if="todo.is_completed">Выполнено</span>
              <span v-else>Не выполнено</span>
            </td>
            <td>
              <div class="btn-group" role="group">
                <button type="button"
                        class="btn btn-secondary btn-sm"
                        v-b-modal.todo-modal
                        @click="updateForm(todo)">
                    Обновить
                </button>
                &nbsp;
                <button type="button"
                        class="btn btn-danger btn-sm"
                        @click="deleteTodo(todo)">
                  X
                </button>
              </div>
            </td>
          </tr>
        </tbody>
      </table>
    </div>

    <b-modal ref="TodoModal"
             id="todo-modal"
             :title="formTitle"
             hide-footer>
      <b-form @submit="onSubmit" @reset="onReset" class="w-100">
        <b-form-group id="form-update-description-group"
                      label="Описание:"
                      label-for="form-update-description-input">
          <b-form-input id="form-update-description-input"
                        type="text"
                        v-model="todoData.description"
                        required>
          </b-form-input>
        </b-form-group>
        <b-form-group id="form-update-complete-group">
          <b-form-checkbox-group v-model="todoData.is_completed" id="form-update-checks">
            <b-form-checkbox value="true">Задача выполнена?</b-form-checkbox>
          </b-form-checkbox-group>
        </b-form-group>
        <b-button-group>
          <b-button type="submit" variant="primary" v-if="operation == 'create'">Создать</b-button>
          <b-button type="submit" variant="primary" v-if="operation == 'update'">Обновить</b-button>
          <b-button type="reset" variant="danger">Отмена</b-button>
        </b-button-group>
      </b-form>
    </b-modal>
  </div>
</template>

<script>
import Confirmation from './Confirmation.vue';

export default {
  name: 'Todo',
  data() {
    return {
      todos: [],
      formTitle: '',
      todoData: {
        uid: 0,
        description: '',
        is_completed: false,
      },
      confirmationMessage: '',
      showConfirmation: false,
      operation: '',
      errorState: '',
    };
  },
  methods: {
    showAlert(message) {
      this.confirmationMessage = message;
      this.showConfirmation = true;
    },
    getTodos() {
      if (localStorage.getItem('tasks')) {
        this.todos = JSON.parse(localStorage.getItem('tasks'));
      } else {
        this.todos = [];
      }
    },
    setTodos() {
      localStorage.setItem('tasks', JSON.stringify(this.todos));
    },
    createForm() {
      this.formTitle = 'Добавить задачу';
      this.operation = 'create';
      this.todoData.uid = 0;
      this.todoData.description = '';
      this.todoData.is_completed = false;
    },
    updateForm(todo) {
      this.formTitle = 'Изменить задачу';
      this.operation = 'update';
      this.todoData = todo;
    },
    onSubmit(event) {
      event.preventDefault();
      this.$refs.TodoModal.hide();
      const todoRecord = {};
      if (!this.todoData.uid) {
        const lastTodoId = Math.max.apply(null, this.todos.map(todo => todo.uid));
        todoRecord.uid = (lastTodoId > 0) ? lastTodoId + 1 : 1;
        todoRecord.description = this.todoData.description;
        todoRecord.is_completed = this.todoData.is_completed;
        this.todos.push(todoRecord);
        this.showAlert(`Задача "${todoRecord.description}" добавлена`);
      } else {
        const todoID = this.todos.map(todo => todo.uid).indexOf(this.todoData.uid);
        this.todos[todoID].description = this.todoData.description;
        this.todos[todoID].is_completed = this.todoData.is_completed;
        this.showAlert('Задача обновлена');
      }
      this.setTodos();
      this.getTodos();
    },
    onReset(event) {
      event.preventDefault();
      this.$refs.TodoModal.hide();
      this.getTodos();
    },
    deleteTodo(todo) {
      if (this.todos.map(td => td.uid).indexOf(todo.uid, 0) < 0) {
        this.errorState = 'Некорректный uid';
        return;
      }
      this.todos.splice(this.todos.map(td => td.uid).indexOf(todo.uid, 0), 1);
      this.setTodos();
      this.showAlert('Задача удалена');
    },
  },
  created() {
    this.getTodos();
  },
  computed: {
    numTasks() {
      return this.todos.length;
    },
    numOngoing() {
      return this.todos.map(todo => todo.is_completed)
        .filter(IsCompleted => IsCompleted === false).length;
    },
  },
  components: {
    confirmation: Confirmation,
  },
};
</script>

<style>
button#task-add {
  margin-top: 20px;
  margin-bottom: 20px;
}
h1, td {
  text-align: left;
}
.todo-uid {
  text-align: right;
}
</style>
