<template>
  <div>
    <b-alert v-model="show" variant="success" dismissible>{{ message }}</b-alert>
  </div>
</template>

<script>
export default {
  computed: {
    show: {
      get() {
        return this.showConfirmation;
      },
      set() {
        this.$parent.showConfirmation = false;
      },
    },
  },
  props: ['message', 'showConfirmation'],
};
</script>
